<template>
  <div class="addBankcard">
    <div class="card">
      <div class="card-item">
        <img src="../../assets/img/my/identificationCard.png" alt="身份证">
        <div>账户更名</div>
      </div>
      <div class="card-item">
        <img src="../../assets/img/my/bink.png" alt="银行卡">
        <div>添加银行卡</div>
      </div>
    </div>
    <div class="rule">
    <div class="rule-left">
      <img src="../../assets/img/my/edit.png" alt="编辑">
      <div class="rule-flex">
        <span>规则介绍</span>
        <img src="../../assets/img/my/triangle.png" alt=""></div>
    </div>
    <div class="rule-right">
      <div>微信支付的姓名异常或修改过如···</div>
      <div>微信实名15位的身份证如何升级···</div>
      <div class="rule-padding-none">提示“银行卡有误”如何处理···</div>
    </div>
  </div>
    <div class="rule">
      <div class="rule-left">
        <img src="../../assets/img/my/card1.png" alt="编辑">
        <div class="rule-flex">
          <span>绑定类</span>
          <img src="../../assets/img/my/triangle.png" alt=""></div>
      </div>
      <div class="rule-right">
        <div>基础介绍</div>
        <div>证件号/姓名不符</div>
        <div class="rule-padding-none">提示“卡号不符/异常提示</div>
      </div>
    </div>
    <div class="rule">
      <div class="rule-left">
        <img src="../../assets/img/my/card1.png" alt="编辑">
        <div class="rule-flex">
          <span>国际类</span>
          <img src="../../assets/img/my/triangle.png" alt=""></div>
      </div>
      <div class="rule-right">
        <div>提示国际卡已过期怎么处理？</div>
        <div>国际卡用户忘记密码是如何找回</div>
        <div class="rule-padding-none">国际信用卡支持哪些支付功能？</div>
      </div>
    </div>
    <div class="service" @click="consult()">
      <img src="../../assets/img/my/boy.png" alt="男孩">
      <span>客服咨询</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "addBankcard",
    data() {
      return {

      }
    },
    methods:{
      consult(){
        this.$router.push({path:'/consult'});
      }
    },
  }
</script>

<style scoped lang="less">
  .addBankcard{
    min-height: 100vh;
    .card{
      display: flex;
      justify-content: space-between;
      width: 90%;
      padding: 14px 0 10px 0;
      margin: 0 5% 0 5%;
      font-size: 15px;
      text-align: center;
      border-bottom: 1px solid #D2D2D2;
      .card-item{
        margin: 0 35px;
      }
    }
    .rule{
      padding: 15.5px 0;
      width: 90%;
      margin: 0 5%;
      font-size: 15px;
      border-bottom: 1px solid #D2D2D2;
      .rule-left{
        display: inline-block;
        padding-right: 10px;
        width: 20%;
        text-align: center;
        .rule-flex{
          img{
            vertical-align: baseline;
          }
          /*display: flex;*/
          /*align-items: center;*/
        }
      }
      .rule-right{
        display: inline-block;
        width: 75%;
        div{
          padding-bottom: 11.5px;
          border-bottom: 1px solid #D2D2D2;
        }
        .rule-padding-none{
          padding: 0;
          border: none;
        }
      }
    }
    .service{
      position: fixed;
      bottom: 0;
      height: 50px;
      line-height: 50px;
      background-color: #EDEDED;
      width: 100%;
      text-align: center;
      color: #2F6FFF;
      font-size: 18px;
      img{
        vertical-align: text-bottom;
        margin-right: 11.5px;
      }
    }
  }
</style>



